.chat {
	flex: 0.7;
	flex-grow: 1;
	overflow-y: scroll;
	padding-bottom: 150px;
}
.chat-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 20px;
	border-bottom: 1px solid lightgray;
}
.chat-headerleft {
}
.chat-channelName {
	display: flex;
	text-transform: lowercase;
}

.chat-channelName > .MuiSvgIcon-root {
	margin-left: 10px;
	font-size: 18px;
}

.chat-headerRight > p {
	display: flex;
	align-items: center;
	font-size: 14px;
}

.chat-headerRight > p > .MuiSvgIcon-root {
	margin-right: 5px !important;
	font-size: 16px;
}
