.sidebarOption {
	display: flex;
	align-items: center;
	font-size: 12px;
	padding-left: 2px;
	cursor: pointer;
}

.sidebarOption:hover {
	opacity: 0.9;
	background-color: #340e36;
}
.sidebarOption > h3 {
	font-weight: 500;
}

.sidebarOption-icon {
	padding: 10px;
	font-size: 15px !important;
}

.sidebarOption-channel {
	padding: 10px 0;
}

.sidebarOption-hash {
	padding: 10px;
}
